.navBar-container {
  margin-bottom: 30px;
  flex-wrap: wrap;
}

.nav {
  display: flex;
  padding: 20px;
  flex-wrap: wrap;
  justify-content: space-between;
}

.navBarTitle {
  height: auto;
  width: fit-content;
  font-size: 2rem;
}

.navBarTitle h2 {
  color: rgb(108, 11, 6);
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  font-weight: 650;
}

.navItems {
  display: flex;
  margin-left: auto;
  margin-top: auto;
  font-size: 2rem;
  flex-wrap: wrap;
}

.navItems li {
  margin: auto;
  padding: 4px;
  justify-content: space-evenly;
}

.navBarName {
  margin-left: auto;
  margin-right: auto;

  font-size: 40px;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 965px) {
  .navBarTitle {
    display: block;
    width: 100%;
    text-align: center;
  }
}

@media screen and (max-width: 965px) {
  .navbarLinks {
    justify-content: center;
    width: 100%;
    text-align: center;
  }
}

@media screen and (max-width: 768px) {
  .navBarTitle {
    display: none;
  }

  .navItems li {
    border: solid 2px;
    border-radius: 5px;
    padding: 3px;
    margin-bottom: 5px;
    margin-top: 5px;
  }
}
